import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import { Message } from 'element-ui'

Vue.use(Router)
const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/fileLoader',
      name: 'fileLoader',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '../views/FileLoader.vue')
    },
    {
      path: '/manualLoader',
      name: 'manualLoader',
      component: () => import('../views/ManualLoader.vue')
    },
    {
      path: '/introduce',
      name: 'Introduce',
      component: () => import('../views/Introduce.vue')
    }
  ]
})

// 路由守卫
router.beforeEach((to, from, next) => {
  const isLogin = sessionStorage.getItem('username') !== null && sessionStorage.getItem('username') !== 'null'
  if (!isLogin && to.name !== 'login') {
    Message.warning('请优先登录\nPlease log in first')
    // 如果没有登录，则跳转到登录页
    router.push({ name: 'login' })
  } else if (isLogin && to.name === 'login') {
    // 如果为登录态跳转回登录页，则认为其为登出操作
    sessionStorage.setItem('username', null)
    sessionStorage.setItem('userToken', '')
  }
  next()
})

export default router
