<template>
  <el-container class="login-page">
    <el-header class="logo-wrap">
      <el-row>
        <el-col>
          <img src="../assets/logo.png" alt="上海工业控制安全创新科技有限公司">
          <div class="software-name">
            基于构件的工业软件复杂度计算工具软件 v1.0
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-main>
      <el-row>
        <el-col class="login-box" :span="8" :offset="8">
          <el-form label-position="left" label-width="80px" :model="formLabel">
            <el-form-item label="用户名">
              <el-input v-model="formLabel.username"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input v-model="formLabel.password" type="password" show-password @keyup.enter.native="login"></el-input>
            </el-form-item>
          </el-form>
          <el-button type="primary" @click="login()" >登录</el-button>
        </el-col>
      </el-row>
    </el-main>
    <el-footer>
      <el-row type="flex" class="footer-options">
        <el-col :span="2" :offset="9">
          <el-button type="text" @click="showDialog('company')">公司介绍</el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="text" @click="showDialog('service')">服务协议</el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="text" @click="showDialog('copyright')">版权声明</el-button>
        </el-col>
      </el-row>
      <el-row class="footer-copyright">
        <div>Copyright © 2019 上海工业控制安全创新科技有限公司</div>
      </el-row>
    </el-footer>

    <el-dialog
      :title="dialogContent.title"
      :visible.sync="centerDialogVisible"
      width="60%"
      append-to-body
      center>
      <div class="dialog-logo-wrap" v-if="dialogContent.title === '公司介绍'">
        <img src="../assets/logo.png" alt="上海工业控制安全创新科技有限公司">
      </div>
      <span>{{dialogContent.content}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
import crypto from 'crypto'

export default {
  name: 'Login',
  data () {
    return {
      formLabel: { // 登录表单
        username: '',
        password: ''
      },
      centerDialogVisible: false, // 是否展示对话框
      dialogContent: { // 对话框展示内容
        title: '',
        content: ''
      }
    }
  },
  methods: {
    /**
     * 用户登录
     */
    login () {
      const username = this.formLabel.username
      const password = this.formLabel.password
      if (username === 'admin' && password === 'admin') {
        this.$message.success('登录成功')
        // 生成token
        const sha1 = crypto.createHash('sha1')
        const salt = 'pMm6kWsoWjR18sWKnoG4Az=='
        const beforeCrypto = salt + username + password
        const afterCrypto = sha1.update(beforeCrypto).digest('base64')
        // 存储进session
        sessionStorage.setItem('username', 'admin')
        sessionStorage.setItem('userToken', afterCrypto)
        console.log(111, sessionStorage.getItem('username'))
        // 存储进vuex
        this.$store.dispatch('setUser', username)
        this.$router.push({ path: '/' })
      } else {
        this.$message.error('错误的用户名密码！')
        this.formLabel = {
          username: '',
          password: ''
        }
      }
    },
    showDialog (dialogContentType) {
      console.log(dialogContentType)
      if (dialogContentType === 'company') {
        this.centerDialogVisible = true
        this.dialogContent = {
          title: '公司介绍',
          content: '       上海工业控制系统安全创新功能型平台是上海市和普陀区两级共建的市级平台，主要围绕功能安全和信息安全领域的核心技术进行研发与转化。联合上下游企业，提供技术研发、仿真验证、监测预警、检测评估、培训咨询、产业对接等服务，帮助汽车电子、轨道交通、航空航天等重点行业和关键基础设施部门，提升工业生产制造和终端产品全生命周期的安全可靠，以及加固工业互联网和物联网的信息安全，打造连接产学研用政资等相关主体的"安全＋"产业生态服务平台。'
        }
      } else if (dialogContentType === 'service') {
        this.centerDialogVisible = true
        this.dialogContent = {
          title: '服务协议',
          content: '       本服务条款构成我们之间的合同，本服务条款包括本页面所述全部条款和工具软件隐私条款、商业条款、商标使用及其他可能不时向您提供并且您接受的与特定服务产品相关的条款或条件（我们将这些统称为"服务条款"）中的规定。您的下载、安装、获取或使用工具软件开发或运营的软件（统称"工具软件"），注册或登录等行为即视为您阅读并同意本服务条款的约束，包括工具软件对本服务条款随时所做的任何修改。如果您不同意接受本服务条款或工具软件对本服务条款所做的任何修改，您应立即停止使用印象笔记服务、关闭帐户并卸载工具软件。\n' +
            '       如果您使用印象笔记相关的产品或服务或参加我们的用户论坛，您可能还需要与我们另行签订一份协议（"独立协议"），成为该独立协议的一方，且本服务条款不得视为取代独立协议的具体合同条款。如果独立协议与本服务条款之间存在任何冲突，则独立协议的条款优先于本服务条款中的冲突条款。\n'
        }
      } else if (dialogContentType === 'copyright') {
        this.centerDialogVisible = true
        this.dialogContent = {
          title: '版权申明',
          content: '       警告：此计算机程序到版权法与国际公约保护。未经授权重制或散布此程序或其任何部分，可能会导致严重的民事和刑事处置。'
        }
      }
    }
  }
}
</script>

