<template>
  <div class="page-header">
    <div class="header-title">
      {{$t('title')}}
    </div>
    <div class="header-settings">
      <div class="setting-language">
        <el-select v-model="locale" placeholder="请选择语言" size="small">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="setting-login">
        <div class="login-wrap" v-if="!isLogin">
          <router-link to="/login" class="login-button">
            <el-button type="success">{{$t('login')}}</el-button>
          </router-link>
        </div>
        <div class="user-home" v-if="isLogin">
          <div class="user-name">
            <span style="margin-right: 5px"><i class="el-icon-user"></i></span>
            <span>{{username}}</span>
          </div>
          <router-link to="/login" class="logout-button">
            <el-button type="danger" size="small">{{$t('logout')}}</el-button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageHeader',
  data () {
    const username = sessionStorage.getItem('username')
    return {
      isLogin: username !== null && username !== 'null',
      username: username,
      locale: '',
      options: [{
        value: 'cn',
        label: '简体中文'
      }, {
        value: 'us',
        label: 'English'
      }]
    }
  },
  watch: {
    locale: {
      handler (newVal) {
        this.$i18n.locale = newVal
      }
    }
  }
}
</script>

<style scoped lang="stylus">
